import Image from 'next/image'
import type { HomepageConfig } from '@/lib/content'

type AboutContent = HomepageConfig['about']
type ColumnItem = AboutContent['columns'][number]['items'][number]

function ItemBlock({ item }: { item: ColumnItem }) {
  return (
    <div className="mt-4 first:mt-0">
      <p className="font-semibold text-stone-100">{item.title}</p>
      {item.desc != null && (
        <p className="mt-0.5 text-sm text-stone-400">{item.desc}</p>
      )}
      {item.href != null && (
        <a
          href={item.href}
          className="mt-0.5 block text-sm text-stone-300 underline underline-offset-2 hover:text-white"
        >
          {item.linkText ?? (item.href.startsWith('mailto:') ? item.href.replace('mailto:', '') : item.href)}
        </a>
      )}
      {item.text != null && (
        <p className="mt-0.5 text-sm text-stone-400">{item.text}</p>
      )}
      {item.links != null && item.links.length > 0 && (
        <p className="mt-0.5 flex flex-wrap gap-x-4 gap-y-0 text-sm">
          {item.links.map(({ label, href }) => (
            <a
              key={href}
              href={href}
              className="text-stone-300 underline underline-offset-2 hover:text-white"
            >
              {label}
            </a>
          ))}
        </p>
      )}
    </div>
  )
}

export default function About({ content }: { content: AboutContent }) {
  return (
    <footer id="about" className="border-t border-stone-700 bg-stone-900 px-4 py-12 text-stone-300 md:py-16">
      <div className="mx-auto flex max-w-5xl flex-col gap-10 md:flex-row md:gap-16">
        {/* 左侧：Logo + 品牌名 + 版权 */}
        <div className="flex-shrink-0 md:w-64">
          <Image
            src={content.logo}
            alt={content.logoAlt}
            width={140}
            height={56}
            className="h-12 w-auto object-contain md:h-14"
          />
          <p className="mt-3 text-lg font-semibold text-white">{content.name}</p>
          {content.nameEn != null && content.nameEn !== '' && (
            <p className="mt-0.5 text-sm text-stone-400">{content.nameEn}</p>
          )}
          <div className="mt-4 space-y-1 text-sm text-stone-500">
            {content.copyright.map((line, i) => (
              <p key={i}>{line}</p>
            ))}
          </div>
        </div>

        {/* 右侧：多列 */}
        <div className="flex min-w-0 flex-1 flex-wrap gap-8 md:gap-12">
          {content.columns.map((col) => (
            <div key={col.title} className="min-w-0 flex-1 basis-48">
              <h3 className="text-base font-bold text-white">{col.title}</h3>
              <div className="mt-2 h-px w-8 bg-stone-600" />
              {col.items.map((item, i) => (
                <ItemBlock key={`${item.title}-${i}`} item={item} />
              ))}
            </div>
          ))}
        </div>
      </div>
    </footer>
  )
}
